import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import * as $ from 'jquery';
import {TranslateService} from '@ngx-translate/core';
import {VcardService} from '../vcard.service';

@Component({
  selector: 'navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  lang: string;
  collapsed = true;

  toggleCollapsed(): void {
    this.collapsed = !this.collapsed;
  }

  constructor(route: ActivatedRoute, private router: Router, private translate: TranslateService, public vcard: VcardService) {
    this.lang = route.snapshot.params['lang'];
  }


  ngOnInit() {
    this.setOtherLang();
  }

  setOtherLang() {
    if (this.lang === 'rs') {
      this.lang = 'en';
      $('.flag-icon').removeClass('flag-icon-rs').addClass('flag-icon-gb');
    } else {
      this.lang = 'rs';
      $('.flag-icon').removeClass('flag-icon-gb').addClass('flag-icon-rs');
    }
  }

  changeLanguage() {
    this.translate.use(this.lang);
    this.router.navigate(['', this.translate.currentLang]);
    this.setOtherLang();
  }
}
