import {Component, OnInit} from '@angular/core';
import {isUndefined} from 'util';
import {ActivatedRoute, Router} from '@angular/router';
import {BsModalService} from 'ngx-bootstrap/modal';
import {TranslateService} from '@ngx-translate/core';
import {Title} from '@angular/platform-browser';
import {PlatformLocation} from '@angular/common';

@Component({
  selector: 'home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  lang: string;

  constructor(private router: Router, private modalService: BsModalService, private route: ActivatedRoute,
              private translate: TranslateService, private title: Title, private platformLocation: PlatformLocation) {
    this.lang = this.route.snapshot.params['lang'];
  }

  ngOnInit() {
    this.title.setTitle('Resume.io');
    if (isUndefined(this.lang) || (this.lang != 'pl' && this.lang != 'en')) {
      this.setDefaultBrowserLanguage();
    } else {
      console.log('Lang set to: ' + this.lang);
      this.translate.use(this.lang);
    }
  }

  private setDefaultBrowserLanguage() {
    if (this.translate.getBrowserLang() == 'pl') {
      this.lang = 'pl';
    } else {
      this.lang = 'en';
    }
    console.log('Lang set to: ' + this.lang);
    this.translate.use(this.lang);
    this.router.navigate(['', this.lang]);
  }

  downloadPdf() {
    window.location.href = '/resume-io/assets/pdf/CV Marczynski Krzysztof - Java Developer - ' + this.route.snapshot.params['lang'].toUpperCase() + '.pdf';
  }
}
