import {Component, OnInit} from '@angular/core';
import * as $ from 'jquery';
import {ActivatedRoute, Router} from '@angular/router';
import {TranslateService} from '@ngx-translate/core';
import {PrintService} from '../print.service';

@Component({
  selector: 'app-print-resume',
  templateUrl: './print-resume.component.html',
  styleUrls: ['./print-resume.component.css']
})
export class PrintResumeComponent implements OnInit {

  constructor(private router: Router, private route: ActivatedRoute, private translate: TranslateService, private printService: PrintService) {
  }

  ngOnInit() {
    this.printDiv();
  }

  printDiv() {
    $('#name-and-position-container').removeClass('col-sm-7').addClass('col-5');
    $('#contact-info').removeClass('col-sm-5').addClass('col-4');

    for (let i = 1; i <= 12; ++i) {
      $('.col-sm-' + i).removeClass('col-sm-' + i).addClass('col-' + i);
      $('.col-md-' + i).removeClass('col-md-' + i).addClass('col-' + i);
    }
    this.printService.printAfterImagesLoaded(this.preparePrintTitle(), this.route.snapshot.params['lang']);
  }

  preparePrintTitle(): string {
    return this.translate.instant('default-texts.resume-short') + ' '
      + this.translate.instant('resume.contact.last-name-latinized') + ' '
      + this.translate.instant('resume.contact.first-name') + ' - '
      + this.translate.instant('resume.contact.job-position') + ' - '
      + String(this.translate.currentLang).toUpperCase();
  }


}
