import {Injectable} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class VcardService {

  constructor(private translate: TranslateService) {
  }

  generateVCard() {
    let filename = 'Veljko Siracki vcard.vcf';
    this.download(filename, this.prepareVCard(), 'text/vcard');
  }

  private download(filename: string, text: string, mimeType: string) {
    let element = document.createElement('a');
    element.setAttribute('href', 'data:' + mimeType + ';charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
  }

  private createVCardstring(firstName: string, lastName: string, title: string, email: string, phone: string, website: string) {
    let vcard = 'BEGIN:VCARD\nVERSION:2.1\n';
    vcard += 'N:' + lastName + ';' + firstName + '\n';
    vcard += 'FN:' + firstName + '' + lastName + '\n';
    vcard += 'TITLE:' + title + '\n';
    vcard += 'TEL:' + phone + '\n';
    vcard += 'EMAIL:' + email + '\n';
    vcard += 'URL:' + website + '\n';
    vcard += 'END:VCARD';
    return vcard;
  }

  private prepareVCard() {
    return this.createVCardstring(
      this.translate.instant('resume.contact.first-name'),
      this.translate.instant('resume.contact.last-name-latinized'),
      this.translate.instant('resume.contact.job-position'),
      this.translate.instant('resume.contact.email'),
      this.translate.instant('resume.contact.phone'),
      this.translate.instant('resume.contact.personal-website'));
  }
}
