import {Injectable} from '@angular/core';
import {Router} from '@angular/router';
import {Title} from '@angular/platform-browser';

@Injectable({
  providedIn: 'root'
})
export class PrintService {
  lang: string;

  onafterprint = (): void => {
    this.router.navigate(['', this.lang]);
  }

  constructor(private router: Router, private titleService: Title) {
  }

  printAfterImagesLoaded(title: string, lang: string) {
    setTimeout(() => {
      let elems = document.getElementsByTagName('img');
      let proceed = true;
      for (let elemsKey = 0; elemsKey < elems.length; elemsKey++) {
        if (!elems[elemsKey].complete) {
          proceed = false;
          this.printAfterImagesLoaded(title, lang);
        }
        if (proceed && elems[elemsKey].naturalWidth === 0) {
          proceed = false;
          return this.printAfterImagesLoaded(title, lang);
        }
      }
      if (proceed) {
        this.lang = lang;
        this.titleService.setTitle(title);
        window.addEventListener('afterprint', this.onafterprint, true);
        window.print();
        setTimeout(() => this.router.navigate(['', this.lang]), 2000);
      }
    }, 1000);

  }
}
