import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {ModalModule} from 'ngx-bootstrap/modal';

import {AppComponent} from './app.component';
import {ResumeHeaderComponent} from './resume/resume-header/resume-header.component';
import {WorkExperienceComponent} from './resume/work-experience/work-experience.component';
import {EducationComponent} from './resume/education/education.component';
import {CoursesComponent} from './resume/courses/courses.component';
import {SkillsComponent} from './resume/skills/skills.component';
import {SummaryComponent} from './resume/summary/summary.component';
import {LanguagesComponent} from './resume/languages/languages.component';
import {InterestsComponent} from './resume/interests/interests.component';
import {ResumeComponent} from './resume/resume.component';
import {PortfolioComponent} from './portfolio/portfolio.component';
import {NavbarComponent} from './navbar/navbar.component';
import {AppRoutingModule} from './app-routing/app-routing.module';
import {HomeComponent} from './home/home.component';
import {PrintResumeComponent} from './print-resume/print-resume.component';
import {TooltipModule} from 'ngx-bootstrap/tooltip';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {HttpClient, HttpClientModule} from '@angular/common/http';
import {QRCodeModule} from 'angularx-qrcode';
import {BusinessCardComponent} from './business-card/business-card.component';
import {ServiceWorkerModule} from '@angular/service-worker';
import {environment} from '../environments/environment';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    AppComponent,
    ResumeHeaderComponent,
    WorkExperienceComponent,
    EducationComponent,
    CoursesComponent,
    SkillsComponent,
    SummaryComponent,
    LanguagesComponent,
    InterestsComponent,
    ResumeComponent,
    PortfolioComponent,
    NavbarComponent,
    HomeComponent,
    PrintResumeComponent,
    BusinessCardComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    QRCodeModule,
    ModalModule.forRoot(),
    TooltipModule.forRoot(),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (HttpLoaderFactory),
        deps: [HttpClient]
      }
    }),
    AppRoutingModule,
    ServiceWorkerModule.register('./ngsw-worker.js', { enabled: environment.production })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {
}
